<template>
	<div class="detail">
		<header :style="{background:mainColor}" class="header">
			<van-icon name="arrow-left" @click="$router.back()" />
			<p>文章详情</p>
		</header>
		<div class="w">
			<h2 class="title">{{item.title}}</h2>
			<div class="author">
				<div class="left">
					<img :src="avatar" alt="">
					<p>{{item.author}}</p>
					<br>
					<span>{{new Date(Number(item.time)).toLocaleDateString()}}</span>
				</div>
				<div class="right">
					<van-button round type="default" size="small">
						<van-icon name="plus" />
						已关注
					</van-button>
				</div>
			</div>
			<div class="text">
				{{text}}
			</div>
			<div v-show="item.imageSrc" class="photo">
				<img :src="imgSrc" v-for="(imgSrc,i) in item.imageSrc" :key="i">
			</div>
		</div>
		<div class="comment">
			<ul>
				<li v-for="item in commentList" :key="item._id">
					<img :src="item.info.avatar" alt="">
					<div class="content">
						<p class="username">{{item.info.nickname}}</p>
						<p class="con">{{item.content}}</p>
						<p class="time">{{new Date(item.create_time).toLocaleDateString()}} <span>{{item.reply_num}}
								回复</span></p>
					</div>
					<div class="nice_count">
						<van-icon name="good-job-o" />
						<!-- <van-icon name="good-job" color="#ef092c" /> -->
						<span>{{item.like_count}}</span>
					</div>
				</li>
			</ul>
		</div>
		<footer class="footerHandle">
			<input type="text" placeholder="写评论">
			<van-icon name="comment-o" />
			<van-icon name="star-o" v-show="!isCollect" @click="collect" />
			<van-icon name="star" v-show="isCollect" color="#0078d4" @click="collect" />
			<van-icon name="like-o" v-show="!isLike" @click="like" />
			<van-icon name="like" v-show="isLike" color="#ef092c" @click="like" />
			<van-icon name="share-o" />
		</footer>
	</div>
</template>

<script>
	import axios from "axios"
	import {
		mapState
	} from "vuex";
	import {
		Toast,
		// ShareSheet
	} from 'vant';
	export default {
		props: ['item'],
		name: 'detail',
		data() {
			return {
				text: '',
				isCollect: false,
				isLike: false,
				commentList: [],
				avatar: ''
			}
		},
		// components: {
		// 	[ShareSheet.name]: ShareSheet
		// },
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		methods: {
			collect() {
				if (this.isCollect) {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav',
						data: {
							article_id: this.item._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							console.log(res.data);
							if (!res.data.code) {
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								this.isCollect = false;
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav',
					data: {
						"article_id": this.item._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						console.log(res.data);
						if (!res.data.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.isCollect = true;
							return
						}
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			like() {
				if (this.isLike) {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike',
						data: {
							article_id: this.item._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							console.log(res.data);
							if (!res.data.code) {
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								this.isLike = false;
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like',
					data: {
						"article_id": this.item._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						if (!res.data.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.isLike = true;
							return
						}
						console.log(res.data);
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			comment() {
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
					data: {
						"article_id": this.item._id,
					}
				}).then(
					res => {
						console.log(res.data.data);
						this.commentList = res.data.data;
					}
				)
			}
		},
		mounted() {
			// console.log(this.item);
			axios({
				method: 'post',
				url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
				data: {
					"article_id": this.item._id,
					user_id: this.person_info._id
				}
			}).then(
				response => {
					// console.log(response.data);
					if (response.data.data) {
						this.avatar = response.data.data.avatar;
						this.isCollect = response.data.data.is_fav;
						this.isLike = response.data.data.is_like;
						if (response.data.data) {
							this.text = response.data.data.content;
						}
					}
				}
			)
			this.comment()
		}
	}
</script>

<style scoped lang="less">
	.detail {
		width: 100%;
		margin-top: 50px;

		.w {
			padding: 0 15px;
		}

		.title {
			margin: 20px;
		}

		.author {
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 60px;

			.left {
				// display: flex;
				width: 40%;
				flex: 1;

				img {
					float: left;
					width: 60px;
					height: 60px;
					border-radius: 50%;
					margin-top: 10px;
					margin-right: 10px;
				}

				p {
					padding-top: 12px;
				}
			}
		}

		.photo {

			img {
				width: 100%;
			}
		}
	}

	.text {
		padding: 20px 15px;
	}

	.comment {
		margin-bottom: 60px;
		margin-top: 20px;

		li {
			display: flex;
			justify-content: space-between;
			padding: 0 15px;
			margin: 14px 0;

			img {
				width: 50px;
				height: 50px;
			}

			.content {
				flex: 1;
				margin-left: 15px;

				.username {
					font-size: 14px;
					color: #7f9ecb;
				}

				.con {
					margin: 3px 0;
				}

				.time {
					font-size: 12px;

					span {
						display: inline-block;
						padding: 2px 6px;
						border: 1px solid #ccc;
						border-radius: 10px;
						margin-left: 5px;
					}
				}
			}
		}
	}
</style>
